const firstletter = '';


/**
 * 
 * @param str 
 * @return  {unicode:Number,firstletterString}
 */
function getLetter(str){
	if (!str || /^ +$/g.test(str)) {
		return ''
	}
	
	// 
	const result = []
	const unicode = str.charCodeAt(0)
	let ch = str.charAt(0)
	if (unicode >= 19968 && unicode <= 40869) {
		ch = firstletter.charAt(unicode - 19968)
	} else if ((unicode >= 97 && unicode <= 122) || (unicode >= 65 && unicode <= 90)) {
		ch = ch.toLocaleUpperCase()
	} else {
		ch = '#'
	}
	const obj = {
		unicode: unicode,
		firstletter: ch
	}
	return obj
}


export default {
	getLetter
}
